.btn {
	&--link {
		width: 120px;
		height: 34px;
		background-color: #fff;
		border: 1px solid $greyish;
		border-radius: 17px;
		box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
		overflow: hidden;
		margin: 0 auto;

		& > a {
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		&__icon {
			width: 20px;
			height: 20px;
			background-image: url('../images/icons/icon-pin-sm.svg');
			transform: translateY(-2px) translateX(-4px);
		}

		&__text {
			font-size: 12px;
			font-weight: bold;
		}
	}
}
